import React from "react";
import { Avatar, Dropdown, Layout, Typography, Form } from "antd";
import {
  ExportOutlined,
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { useRouter } from "next/router";
import { useDispatch, useSelector } from "react-redux";
import { updateSidebar, updateUser } from "@action";
import { CSelect } from "@components";
import CApi from "@api";

const { Header } = Layout;
const { Text } = Typography;
const CHeader = ({ collapsed, img, toggle, onOrganitation }) => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);
  const { organization } = useSelector((state) => state.organization);
  const router = useRouter();

  const navigationToHome = (e) => {
    e.preventDefault();
    router.push("/");
  };
  const navigationToProfile = (e) => {
    e.preventDefault();
    router.push("/profile");
  };
  const handleLogout = async (e) => {
    e.preventDefault();
    await CApi({
      method: "POST",
      url: "api/logout",
    }).then((_res) => {
      router.push("/login");
      dispatch(updateUser({ name: "", email: "", role: 0 }));
      dispatch(
        updateSidebar([
          {
            label: "Dashboard",
            icon: "pie-chart",
            path: "/",
          },
        ])
      );
    });
  };
  const items = [
    {
      label: (
        <div onClick={navigationToProfile}>
          <UserOutlined /> Profile
        </div>
      ),
      key: "Profile",
    },
    {
      label: (
        <div onClick={handleLogout}>
          <ExportOutlined /> Logout
        </div>
      ),
      key: "Logout",
    },
  ];

  return (
    <Header
      style={{ position: "fixed", zIndex: 1, width: "100%" }}
      className="header"
    >
      {collapsed ? (
        <MenuUnfoldOutlined className="trigger" onClick={toggle} />
      ) : (
        <MenuFoldOutlined className="trigger" onClick={toggle} />
      )}
      <a href="/" onClick={navigationToHome}>
        <div className="logo" />
        <span style={{ color: "#ffffff" }}> - </span>
      </a>
      <Dropdown
        className="c-head"
        menu={{
          items,
        }}
        trigger={["click"]}
      >
        <a
          href="/"
          className="ant-dropdown-link"
          onClick={(e) => e.preventDefault()}
        >
          {img ? (
            <Avatar alt={user.name} src={img} size={40} />
          ) : (
            <Avatar size={40} icon={<UserOutlined />} />
          )}
          <span style={{ color: "#ffffff" }}> - </span>
        </a>
      </Dropdown>

      <Text
        style={{ marginRight: 15, marginTop: 20, color: "#000000" }}
        className="c-head"
        strong
      >
        {user.name}
      </Text>
      <div
        className="c-headA"
        style={{ marginRight: 15, marginTop: 18, color: "#000000" }}
      >
        <Form layout="horizontal">
          <CSelect
            label="Organitation"
            placeholder="Organitation"
            // value={organization}
            onChange={onOrganitation}
            options={organization}
          />
        </Form>
      </div>
    </Header>
  );
};

export default CHeader;
